import { OrganizationChartModule } from './modules/organization-chart/organization-chart.module';
import { PrincipalComponent } from './principal.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
    {
        path: '',
        component: PrincipalComponent,
        children: [
            {
                path: '',
                redirectTo: 'dashboard'
            },
            {
                path: 'dashboard',
                loadChildren: () => import('./modules/dashboard/dashboard.module').then(m => m.DashboardModule)
            },
            {
                path: 'corporations',
                loadChildren: () => import('./modules/corporations/corporations.module').then(m => m.CorporationsModule)
            },
            {
                path: 'users',
                loadChildren: () => import('./modules/users/users.module').then(m => m.UsersModule)
            },
            {
                path: 'organization-chart',
                loadChildren: () => import('./modules/organization-chart/organization-chart.module').then(m => m.OrganizationChartModule)
            },
            {
                path: 'applications',
                loadChildren: () => import('./modules/applications/applications.module').then(m => m.ApplicationsModule)
            },
            {
                path: 'settings',
                loadChildren: () => import('./modules/settings/settings.module').then(m => m.SettingsModule)
            },
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PrincipalRoutingModule { }