import { PrincipalComponent } from './../../principal.component';
import { Component, OnInit, HostListener } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { Router } from '@angular/router';

@Component({
    selector: 'app-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss'],
    animations: [
        trigger(
            'inOutAnimation',
            [
                transition(
                    ':enter',
                    [
                        style({
                            top: 80,
                            width: 0,
                            transform: "translateX(-100px)"

                        }),
                        animate('0.3s ease-out',
                            style({
                                top: 80,
                                width: 103,
                                transform: "translateX(0px)"
                            }))
                    ]
                ),
                transition(
                    ':leave',
                    [
                        style({
                            top: 80,
                            width: 103,
                            transform: "translateX(0px)"
                        }),
                        animate('0.3s ease-in',
                            style({
                                top: 80,
                                width: 0,
                                transform: "translateX(-100px)"
                            }))
                    ]
                )
            ]
        ),
        trigger(
            'inOutAnimationSubMenu',
            [
                transition(
                    ':enter',
                    [
                        style({
                            // opacity: 0,
                            // width: 0,
                            transform: "translateX(-180px)"
                        }),
                        animate('0.3s ease-out',
                            style({
                                // opacity: 1,
                                // width: 180,
                                transform: "translateX(0px)"
                            }))
                    ]
                ),
                transition(
                    ':leave',
                    [
                        style({
                            // opacity: 1,
                            // width: 180,
                            transform: "translateX(0px)"
                        }),
                        animate('0.3s ease-in',
                            style({
                                // opacity: 0,
                                // width: 0,
                                transform: "translateX(-180px)"
                            }))
                    ]
                )
            ]
        ),
    ]
})
export class SidebarComponent implements OnInit {
    public screenWidth: any;
    public screenHeight: any;

    get menuExpanded() {
        return PrincipalComponent.menuExpanded;
    }

    get subMenuExpanded(){
        return PrincipalComponent.submenuExpanded
    }

    constructor(
        private router: Router,
    ) { }

    ngOnInit(): void {
        this.screenWidth = window.innerWidth;
        this.screenHeight = window.innerHeight - 80;
        // console.log(this.screenHeight + '  ' + this.screenWidth);

    }

    @HostListener('window:resize', ['$event'])
    onResize(event) {
        this.screenWidth = window.innerWidth;
        this.screenHeight = window.innerHeight - 80;
    }

    onSubMenuShow() {
        PrincipalComponent.submenuExpanded = !PrincipalComponent.submenuExpanded;
    }

    navigateTo(route: string) {
        PrincipalComponent.submenuExpanded = false;
        this.router.navigate([route]);
    }

}
